}

 .boton_izq span::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent #2f353a transparent transparent;
}

.boton_izq:hover {
    color: #fff;
    cursor: pointer;
}

.boton_izq:hover span {
    visibility: visible;
}

