  .eye-container {
    background-color:#f5302e;
    text-align: center;
    margin-left: 20px;
    padding: 5px;
    height: 50px;
    width: 50px;
    border-radius: 50px;
    cursor: pointer;
  }

  .eye {
    fill: none;
    stroke: #F7F7F7;
    stroke-width: 10;
    stroke-miterlimit: 10;
    stroke-linecap: round;
  }
  
  .eye-svg {
    display: block;
    margin: 15px auto;
    width: 40px;
  }
  
  .lid {
    stroke-dasharray: 222;
    stroke-dashoffset: 0;
  }
  
  .pupil {
    stroke-dasharray: 150;
    stroke-dashoffset: 0;
    -webkit-transform: rotate(170deg);
    transform: rotate(170deg);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
  }
  
  .lid--close {
    -webkit-animation: lid--close .5s ease-in-out forwards;
            animation: lid--close .5s ease-in-out forwards;
  }
  .lid--open {
    -webkit-animation: lid--open .5s ease-in-out forwards;
            animation: lid--open .5s ease-in-out forwards;
  }
  
  .pupil--close {
    -webkit-animation: pupil--close .5s ease-in-out forwards;
            animation: pupil--close .5s ease-in-out forwards;
  }
  .pupil--open {
    -webkit-animation: pupil--open .5s ease-in-out forwards;
            animation: pupil--open .5s ease-in-out forwards;
  }
  
  @-webkit-keyframes lid--close {
    from {
      stroke-dashoffset: 0;}
    to {
      stroke-dashoffset: -222;
    }
  }
  
  @keyframes lid--close {
    from {
      stroke-dashoffset: 0;}
    to {
      stroke-dashoffset: -222;
    }
  }
  
  @-webkit-keyframes lid--open {
    from {
      stroke-dashoffset: -222;}
    to {
      stroke-dashoffset: -444;
    }
  }
  
  @keyframes lid--open {
    from {
      stroke-dashoffset: -222;}
    to {
      stroke-dashoffset: -444;
    }
  }
  
  @-webkit-keyframes pupil--close {
    from {
      stroke-dashoffset: 0;}
    to {
      stroke-dashoffset: -150;
    }
  }
  
  @keyframes pupil--close {
    from {
      stroke-dashoffset: 0;}
    to {
      stroke-dashoffset: -150;
    }
  }
  
  @-webkit-keyframes pupil--open {
    from {
      stroke-dashoffset: -150;}
    to {
      stroke-dashoffset: -300;
    }
  }
  
  @keyframes pupil--open {
    from {
      stroke-dashoffset: -150;}
    to {
      stroke-dashoffset: -300;
    }
  }

